.main-nav {

  @media (max-width: @screen-md-max) {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    padding: 45px 15px 20px;
    background-color: rgba(0, 173, 29, 0.95);
    z-index: 1000;
  }

  @media (max-width: @screen-sm-max) {
    width: 100%;
  }

  @media (min-width: @screen-lg) {
    padding-top: 8px;
    margin-left: auto;
    margin-right: 12px;
  }

  @media (min-width: @screen-xl) {
    margin-right: 20px;
    padding-top: 15px;
  }

  &--visible {
    @media (max-width: @screen-md-max) {
      display: block;
    }
  }

  &__list {
    list-style: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;

    @media (min-width: @screen-lg) {
      display: flex;
    }
  }

  &__item {
    padding: 7px 0;

    @media (min-width: @screen-lg) {
      padding: 0;
      margin-right: 12px;

      &:last-child {
        margin-right: 0;
      }
    }

    @media (min-width: @screen-xl) {
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__link {
    display: block;
    color: #fff;
    font-size: 18px;
    transition: all 0.3s;

    &:hover,
    &:active {
      opacity: 0.7;
    }

    @media (min-width: @screen-lg) {
      padding: 2px 3px;
      font-size: 16px;
    }

    @media (min-width: @screen-xl) {
      padding: 5px;
    }
  }
}
