    display: none;
  }
}
.main-nav-toggler > span {
  display: inline-block;
  vertical-align: bottom;
  width: 100%;
  height: 4px;
  background-color: #fff;
  position: relative;
  transition: background .3s .5s;
}
.main-nav-toggler > span:before,
.main-nav-toggler > span:after {
  width: 100%;
  height: 4px;
  background-color: #fff;
  position: absolute;
  left: 0;
  content: '';
  transform-origin: 50% 50%;
  transition: top .3s .6s, transform .3s;
}
.main-nav-toggler > span:before {
  top: 12px;
}
.main-nav-toggler > span:after {
  top: -12px;
}
.main-nav-toggler--close > span {
  transition: background .3s 0;
  background: transparent;
}
.main-nav-toggler--close > span:before,
.main-nav-toggler--close > span:after {
  transition: top .3s, transform .3s .5s;
  top: 0;
}
.main-nav-toggler--close > span:before {
  transform: rotate3d(0, 0, 1, 45deg);
}
.main-nav-toggler--close > span:after {
  transform: rotate3d(0, 0, 1, -45deg);
}
